<template lang="">
  <v-app>
    <v-app-bar class="nav-bar" app rounded dark dense elevation="12">
      <v-icon>fa-solid fa-user</v-icon>1<v-spacer></v-spacer>
      <v-icon>fa-solid fa-user</v-icon>2
    </v-app-bar>
    <div class="app">
      <v-main fluid class="fondo">
        <section class="container">
          <v-list
            :height="height - 24"
            style="background: transparent"
            class="centrar-flex"
          >
            <v-card
              loading
              elevation="12"
              :width="is_movil ? 350 : '80%'"
              style="background: rgb(255 255 255 / 0%); margin-bottom: 80px"
            >
              <template slot="progress">
                <v-progress-linear
                  color="#a30a7b"
                  height="5"
                  indeterminate
                ></v-progress-linear>
              </template>

              <v-col cols="12" style="display: flex; justify-content: center">
                <v-img :src="img"> </v-img>
              </v-col>

              <v-divider class="mx-4"></v-divider>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  dark
                  color="#00000094"
                  title="¿Volveral inicio?"
                  @click="redirecionar('/')"
                >
                  Inicio
                </v-btn>
                <v-progress-circular
                  model-value="12"
                  :size="80"
                  :width="8"
                  color="lime"
                  class="v-progress-circular--visible"
                ></v-progress-circular>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-list>
        </section>
      </v-main>
    </div>
  </v-app>
</template>
<script>
export default {
  name: "ForbiddenPagina",
  data: () => ({
    img: require("@/assets/img/404.png"),
    height: 0,
    is_movil: false,
  }),
  methods: {
    get_size() {
      this.height = window.innerHeight;
      if (window.innerWidth < 600) {
        this.is_movil = true;
      } else {
        this.is_movil = false;
      }
    },
    redirecionar(name) {
      this.$router.push(name);
    },
  },
  mounted() {
    this.get_size();
    window.addEventListener("resize", this.get_size);
  },
  beforeDestroy() {
    window.addEventListener("resize", this.get_size);
  },
};
</script>
<style>
.fondo {
  background: rgb(1, 1, 0);
  background: linear-gradient(
    180deg,
    rgba(1, 1, 0, 0.7469362745098039) 0%,
    rgba(230, 0, 0, 0.38279061624649857) 100%
  );
}
</style>
